<template>
  <div id="app">


<el-dialog :title="'Question:'+qn" v-model="dialogVisible" size="full">
  <p v-html="question"></p>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false">OK</el-button>
  </span>
</el-dialog>


    <el-row :gutter="30">
  <el-col :span="24" >
         <el-table
    :data="records"
      v-loading.body="loading"
    border
     stripe
     @row-click="click"
    style="width: 100%">
   
  
 
 

 
   <el-table-column
      :prop="col"
      :label="col"
      sortable
      v-for="col in column_show"
      >
      </el-table-column>
   <el-table-column
      prop="tags"
      label="Tags"
      sortable
      inline-template
      >
            <el-tag type="success" close-transition v-for="tag in row.tags">{{tag}}</el-tag>

      </el-table-column>
    
  </el-table>
  </el-col>

 
</el-row>




</div>


</template>

<script>
    import Hello from './components/Hello'
    const API_URL = 'static/data.json'
    export default {
        name: 'app',
        components: {
            Hello
        },
        data() {
            return {
                tags: [{
                        key: 1,
                        name: 'hardwareID:5832e993aa780',
                        type: ''
                    }, {
                        key: 2,
                        name: 'Event:Error',
                        type: 'danger'
                    }

                ],
                records: [],
                column_show: [
                  'number','title','level'
                ],
                  dialogVisible: false,
                filters: [],
                loading: true,
                total: 0,
                per_page: 10,
                question:'',
                qn: ''
            }
        },
        mounted() {
            console.log(222)
            this.fetchData(this.filters)
        },
        methods: {
          click(r,e){
                    this.loading = true
                    this.qn = r.number
                    var self = this
             this.$http.get('static/data/'+r._id+'.html').then(this.receiveQuestionData, function() {
                    self.loading = false
                 });
          },
            fetchData(filters) {
                console.log('Fetching=>>>>')
                var self = this
                this.loading = true
                this.$http.get(API_URL).then(this.receiveData, function() {
                    this.loading = false
                });

            },
            receiveData(xhr) {
                this.records = xhr.data
                console.log(xhr)
                    //this.pagination = xhr.data.
                this.total = xhr.data.total
                this.per_page = xhr.data.per_page
                this.loading = false

            },
             receiveQuestionData(xhr) {
                this.question = xhr.body
                console.log(xhr)
                this.loading = false
                this.dialogVisible = true

            }
        }
    }
</script>


